* {
  margin: 0;
  padding: 0;
}

.container {
  grid-template-columns: repeat(2, 200px);
  place-content: center;
  gap: 30px;
  padding: 30px 35px 155px;
  display: grid;
}

@media (width >= 750px) {
  .container {
    grid-template-columns: repeat(2, 200px);
    padding: 30px 160px 155px;
  }
}

@media (width >= 1000px) {
  .container {
    grid-template-columns: repeat(4, 200px);
    padding: 30px 55px 197px;
  }
}

@media (width >= 1500px) {
  .container {
    grid-template-columns: repeat(4, 200px);
    padding: 30px 305px 197px;
  }
}

.person {
  text-align: center;
  flex-direction: column;
  align-items: center;
  transition: transform .3s;
  display: flex;
}

.person:hover {
  background-color: #eee;
  transform: scale(1.2);
}

.person__card {
  position: relative;
}

.person__photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: border-radius 1s;
}

.person__photo:hover {
  border-radius: 60px;
  transition: all 1s;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
  display: flex;
  position: absolute;
  top: 85px;
  right: 10px;
}

.person__name {
  color: #2f4f4f;
  font-family: Arial;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
}

.person__details {
  margin-top: 8px;
}

.person .extra {
  opacity: 0;
  transform: translateY(10px);
}

.person:hover .extra {
  opacity: 1;
  transition: opacity .5s;
  transform: translateY(0);
}
